"use client"

import React, { useEffect, useState } from "react";
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogDescription,
    DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectTrigger, SelectItem, SelectValue, SelectContent } from "@/components/ui/select";
import axioClient from "@/utils/axioClient";

interface Category {
    id: number;
    name: string;
    description: string;
}

interface ConfirmTransactionModalProps {
    isOpen: boolean;
    onClose: () => void;
    parsed: any;
    onSuccess: () => void;
}

export default function ConfirmTransactionModal({ isOpen, onClose, parsed, onSuccess }: ConfirmTransactionModalProps) {
    const [categories, setCategories] = useState<Category[]>([]);
    const [selectedCategory, setSelectedCategory] = useState<string>("");
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        if (isOpen) {
            axioClient.get("/users/1/categories").then((res) => setCategories(res.data));
        }
    }, [isOpen]);

    const handleSubmit = async () => {
        setIsLoading(true);
        try {
            await axioClient.post("/users/1/transactions", {
                amount: parsed.amount,
                recipient: parsed.recipient || "Transaction",
                date: parsed.date,
                categoryId: Number(selectedCategory),
                transactionCost: parsed.transactionCost,
                transactionId:parsed.transactionId,
                type: parsed.type,
                rawMessage: parsed.rawMessage,
                }
            );
            onSuccess();
            onClose();
        } catch (err) {
            console.error(err);
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <Dialog open={isOpen} onOpenChange={onClose}>
            <DialogContent className="sm:max-w-[500px]">
                <DialogHeader>
                    <DialogTitle>Confirm Transaction</DialogTitle>
                    <DialogDescription>Review the extracted details and confirm the transaction.</DialogDescription>
                </DialogHeader>

                <form className="grid gap-4 mt-2">
                    <div className="grid gap-2">
                        <Label>Amount</Label>
                        <Input value={parsed.amount} readOnly />
                    </div>

                    <div className="grid gap-2">
                        <Label>Transaction Cost</Label>
                        <Input value={parsed.transaction_cost} readOnly />
                    </div>

                    <div className="grid gap-2">
                        <Label>Date</Label>
                        <Input value={parsed.date} readOnly />
                    </div>

                    <div className="grid gap-2">
                        <Label>Recipient</Label>
                        <Input value={parsed.recipient || "N/A"} readOnly />
                    </div>

                    <div className="grid gap-2">
                        <Label>Type</Label>
                        <Input value={parsed.type} readOnly />
                    </div>

                    <div className="grid gap-2">
                        <Label>Category</Label>
                        <Select onValueChange={setSelectedCategory} value={selectedCategory}>
                            <SelectTrigger>
                                <SelectValue placeholder="Select a category" />
                            </SelectTrigger>
                            <SelectContent>
                                {categories.map((c) => (
                                    <SelectItem key={c.id} value={String(c.id)}>
                                        {c.name}
                                    </SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                    </div>
                </form>

                <DialogFooter>
                    <Button variant="outline" onClick={onClose} disabled={isLoading}>Cancel</Button>
                    <Button onClick={handleSubmit} disabled={!selectedCategory || isLoading}>
                        {isLoading ? "Saving..." : "Confirm"}
                    </Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}
